body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  --minute-duration: 60s;
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch--speed-up {
  --minute-duration: 10s;
}

.stopwatch__minute {
  transform-origin: bottom;
  animation: rotate-clock calc(var(--minute-duration) * 60) steps(60) infinite;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  bottom: 50%;
}

.stopwatch__second {
  transform-origin: bottom;
  animation: rotate-clock var(--minute-duration) linear infinite;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  position: absolute;
  bottom: 50%;
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

@keyframes rotate-clock {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
